// HOME PAGE SCSS

/*Prevents iphone from resizing in landscape mode*/
// html {-webkit-text-size-adjust: none;}

/*Apply a natural box layout model to all elements*/
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;

  appearance:none;
  -webkit-appearance:none;
}

*:focus{
  outline:0 !important;
}

/*-----MAIN BODY STYLES-----*/
body{
  // font-size: 16px;
  // font-family: 'Raleway', sans-serif;
  font-family: 'Open Sans', sans-serif;
  // font-family: 'Arial', sans-serif;
  font-weight: 400; /*also loaded 600,700 for bold*/
  margin: 0;
  font-size: 14px;
  &.logged-in {
    .myNavbar {
      position: static;
    }
  }
}
p{
  // line-height: 1.4em;
  line-height: 24px;
}
.behindNav{
  background-color: $accBlue;
  height:56px;
}
.container{
  max-width: 1200px;
  margin:0 $smMargin;
  padding-bottom: 60px;
  // margin-top: 56px;
  // margin-top: 30px;
}
.removePad{
  padding:0;
}
i.icoLgPlus{
  font-size: 1.7em;
}
i.fa-ticket-alt{
  transform:rotate(-45deg);
  font-size: 1.6em;
}

/*-------COLUMN LAYOUT------*/
.row {
  clear: both;
  margin: 0 (-$gutter);
}
.column {
  padding: 0 $gutter;
  // display: inline-block;
}
// .column:first-child{
// 	padding-left:0;
// }
// .column:last-child{
// 	padding-right:0;
// }

@for $i from 1 through 4 {
  $w:($i / 4)*100%;
  .small-#{$i} {width: $w; float:left;}
  // @if $i / 4 == 1{
  // 	.small-#{$i}{padding:0;}
  // }
  // @else{
  // 	.small-#{$i}:first-child{padding-left:0;}
  // }
}

/*----- Clearfix: Force an Element not to collapse -----*/
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

