/*-------------NAV------------*/
nav.myNavbar{
  // background-color: $mainRed;
  background-color: $accBlue;
  z-index: 60;
  // padding-top: 0;
  // padding-bottom: 0;
  padding: 3px 0 0 0;
  min-height:56px;
  position: fixed;
  top:0;
  width:100%;
  ul{
    // padding-top:10px;
    li{
      list-style-type: none;
      margin: 0 48px;
      padding: 14px 0;
      border-bottom: 1px solid rgba(255,255,255,0.5);
      ul.subNav{
        // margin-left: -20px;
        // padding: 10px 0 0 40px;
        margin-top:$smMargin;
        background-color: $bgBlue;
      }
      a{
        // color: rgba(255,255,255,0.7);
        color: white;
        text-decoration: none;
        font-size: 1.5em;
        // font-weight: 700;
        // font-family: 'Oswald', sans-serif;
      }
      &:hover{
        a{
          color: rgba(255,255,255,1);
        }
        ul.subNav li{
          a{
            color: rgba(255,255,255,0.7);
          }
          &:hover a{
            // background-color: $mainRed;
            color: white;
          }
        }
      }
      &.current-menu-item{
        a{
          color: rgba(255,255,255,1);
        }
      }
      &:last-child{
        border:none;
      }
    }
  }
}

.topNav{
  display:none;
  // padding-top: 10px;
  margin: 0;
  // margin-top:16px;
  // padding: 0px 0 10px 20px;
  height: 100vh;
}

.hbMenuWrap{
  padding-left:0;
  padding-right:0;
}
.hbMenu{
  display:inline-block;
  cursor: pointer;
  // padding: 5px 2%;
  padding: 10px 2% 0 2%;
}
.bar1, .bar2, .bar3{
  // background-color: #E5E5E5;
  background-color: white;
  // width: 35px;
  // height: 5px;
  margin: 6px 0px;
  width: 30px;
  height: 3px;
  transition: 0.4s;
  // @include radius(2px);
}
.bar3{
  width:15px;
  transition: .1s;
}
.change{
  .bar1{
    // transform: translate(0px, 11px) rotate(-45deg);
    transform: translate(0px, 9px) rotate(-45deg);
  }
  .bar2{
    // opacity: 0;
    transform:rotate(45deg);
  }
  .bar3{
    // transform: translate(0px, -11px) rotate(45deg);
    // transform: translate(0px, -9px) rotate(45deg);
    // width:30px;
    width:0px;
  }
}
.subNav{
  @include mainRedOpacity(.7);
  border: none;
  display:none;
}

.pagination{
  margin-top: 20px;
  justify-content: center;
}
.searchWrap{
  // margin-left: -20px;
  padding-bottom:20px;
  border-bottom:1px solid rgba(255,255,255,0.5);
  .searchArea{
    // font-size:14px;
    // display:none;
    color:$accBlue;
    input{
      font-size: 14px;
      width:96%;
      margin:0 $smMargin/2;
      margin-right:-18px;
      padding:17px 16px;
      padding-bottom: 15px;
      // height:48px;
      // padding: 0 16px;
      border: 1px solid $lightBlue;
      border-radius: 5px;
      &::placeholder{
        color: $accBlue;
        font-size: 14px;
      }
    }
    i{
      margin-left:-18px;
      transform:translateY(3px);
    }
  }
}


