// --------- HOME ELEMENTS -----------
.homeSection{
  margin: 0 (-$smMargin);
  padding: 50px 0;
  text-align: center;
  h2{
    margin:0;
    padding-bottom: 20px;
    font-size: 30px;
    font-weight: $bold;
    color: $accBlue;
  }
  .content{
    margin: 0 $smMargin;
  }
  &.events{
    background-color: $lightBlue;
    .content{
      margin: 0 $smMargin/2;
      h2{
        padding:0;
      }
      .event{
        margin-top: 16px;
        display:inline-block;
        width:50%;
        padding: 0 $smMargin/2;
        .eventImg{
          background-color: $medBlue;
          width:100%;
          height:164px;
          i{
            margin-top: 60px;
            color:white;
          }
        }
        .caption{
          i{
            float:right;
            margin: 10px 5px;
            color:$accBlue;
          }
          .title{
            margin:0;
            text-align:left;
            font-weight: $bold;
            color:$accBlue;
          }
          .dates{
            text-align:left;
            line-height: 1em;
            font-size: 12px;

          }
        }
      }
      .audition{
        margin: 0 auto;
        margin-top: 50px;
        max-width: 50%;
        p{
          color: $accBlue;
          font-size: 20px;
          font-weight: $bold;
          i{
            font-size: 18px;
          }
        }
      }
    }
  }
  &.features{
    .ctaBtn{
      @include defBtn;
      margin-top: 30px;
    }
    button{
      @include defBtn;
      margin-top:30px;
    }
  }
  &.venues{
    background-color: $accBlue;
    .venue{
      margin:0 auto;
      width:50%;
      .venueImg{
        background-color: $medBlue;
        width:100%;
        height:164px;
        i{
          // margin-top: 37%;
          margin-top: 60px;
        }
      }
      p{
        font-size: 1.4em;
        margin: 8px 0 16px 0;
      }
    }
    h2,p,i{
      color:white;
    }
  }
  &.sponsors{
    .sliderWrap{
      // max-width: 800px;
      // width: 800px;
      width: 100%;
      margin:0 auto;
      overflow: hidden;
      position: relative;
    }
    .sponsorsSlider{
      // width:1330px;
      // $sponsWidth: 1330px;
      $sponsWidth: 2280px;
      width:$sponsWidth;
      .sponsorImg{
        background-color: $medBlue;
        width: 80px;
        height: 80px;
        display: inline-block;
        margin-right: 15px;
        i{
          // margin-top:30%;
          margin-top: 20px;
          color: white;
        }
      }
      @include slider($sponsWidth);
    }
  }
}

.movieRow{
  padding-top: 10px;
  margin-top: 30px;
  border-top: 1px solid rgba(0,0,0,0.5);
  h2{
    margin:0;
  }
  .movieImgWrap{
    float:right;
    max-width: 50%;
